webpackJsonp([212986313302902],{

/***/ 479:
/***/ (function(module, exports) {

	module.exports = {"pathContext":{"posts":[{"html":"<h3>Create a container with a mongo database and connect to it</h3>\n<p>Start with displaying all currently running containers:</p>\n<pre><code class=\"language-bash\">docker ps\n</code></pre>\n<p>Then create locally the container associated with the mongo docker image:</p>\n<pre><code class=\"language-bash\">docker run -p 27017:27017 -d mongo\n</code></pre>\n<p>Executing another time <code>docker ps</code> results in information on the created container like the following being displayed:</p>\n<pre><code>CONTAINER ID        IMAGE               COMMAND                  CREATED             STATUS              PORTS                      NAMES\n2c6a61aba41b        mongo               \"docker-entrypoint.s…\"   3 minutes ago       Up 3 minutes        0.0.0.0:27017->27017/tcp   clever_dubinsky\n</code></pre>\n<p>Connection to the database at url localhost:27017 can be opened using for instance the robo-3t nosql database editor.\nFinally, the container when not needed anymore can be stopped with:</p>\n<pre><code>docker stop 2c6a61aba41b\n</code></pre>\n<h3>Create a container with a PostgreSQL database given the database name and the user credentials</h3>\n<p>Let us assume we need to connect to a database called course_data in a PostgreSQL SGBD with the following credentials: the user is postgres and the password is password. The command line to launch a docker container with such a database is:</p>\n<pre><code class=\"language-bash\">docker run --name postgresdb -e POSTGRES_USER=postgres -e POSTGRES_PASSWORD=password -e POSTGRES_DB=course_data -d -p 5432:5432 postgres\n</code></pre>\n<p>Terminal access to the database prompt is obtained by first accessing the container shell:</p>\n<pre><code class=\"language-bash\">docker container exec -it postgresdb bash\n</code></pre>\n<p>The postgre prompt of user 'postgres' may then be accessed with:</p>\n<pre><code class=\"language-bash\">psql course_data postgres\n</code></pre>","id":"/Users/nicolas/repos/github.com/nperon/workshop/src/pages/2020-03-31-docker-recipes/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2020-03-31T16:09:12.235Z","path":"/docker-recipes","title":"Docker Recipes","excerpt":"Some Docker Recipes","tags":["docker","container","devops"]}},{"html":"<p>The following few steps can be taken as a quick start to Argo CD\nthat can be done with a local cluster, say minikube. </p>\n<h2>Starting argocd non HA with cluster privilege</h2>\n<pre><code class=\"language-zsh\">kubectl apply -n argocd -f https://raw.githubusercontent.com/argoproj/argo-cd/stable/manifests/install.yaml\n</code></pre>\n<h2>Displaying argocd initial admin secret</h2>\n<pre><code class=\"language-zsh\">ENCODED=$(kubectl -n argocd get secret argocd-initial-admin-secret -o yaml | yq .data.password)\necho $ENCODED\necho $ENCODED | base64 --decode\n</code></pre>\n<h2>Exposing argocd server with a port forward argocd server pod</h2>\n<pre><code class=\"language-zsh\">kubectl port-forward svc/argocd-server -n argocd 8080:443\n</code></pre>\n<p>Open browser at <a href=\"https://localhost:8080\">https://localhost:8080</a></p>\n<h2>Using ArgoCD CLI</h2>\n<p>CLI allows to manage everything: applications, repos, clusters, tasks, projects...</p>\n<pre><code class=\"language-zsh\">argocd login localhost:8080\nargocd cluster list\n</code></pre>\n<h2>Applications</h2>\n<p>Given the following yaml content in a file called <code>application.yml</code>:</p>\n<pre><code class=\"language-yaml\">apiVersion: argoproj.io/v1alpha1\nkind: Application\nmetadata:\n  name: guestbook\n  namespace: argocd\nspec: \n  destination:\n    namespace: guestbook\n    server: \"https://kubernetes.default.svc\"\n  project: default\n  source:\n    path: guestbook\n    repoURL: \"https://github.com/mabusaa/argocd-example-apps.git\"\n    targetRevision: master\n  syncPolicy:\n    syncOptions:\n      - CreateNamespace=true\n</code></pre>\n<p>run the following commands:</p>\n<pre><code class=\"language-zsh\">kubectl apply -f application.yml\n</code></pre>\n<p>And then verify the application was created with:</p>\n<pre><code class=\"language-zsh\">kubectl get application -n argocd\n</code></pre>\n<p>Other app based on helm can be started the same way:</p>\n<pre><code class=\"language-zsh\">kubectl apply -f application_helm_options.yml\n</code></pre>\n<p>where <code>application_helm_options.yml</code> has a content like:</p>\n<pre><code class=\"language-yaml\">apiVersion: argoproj.io/v1alpha1\nkind: Application\nmetadata:\n  name: helm-app\n  namespace: argocd\nspec: \n  destination:\n    namespace: helm-app\n    server: \"https://kubernetes.default.svc\"\n  project: default\n  source:\n    path: helm-guestbook\n    repoURL: \"https://github.com/mabusaa/argocd-example-apps.git\"\n    targetRevision: master\n    helm:\n      releaseName: my-release\n  syncPolicy:\n    syncOptions:\n      - CreateNamespace=true\n</code></pre>\n<p>Finally, an application loaded recursively from\na local directory can be started with:</p>\n<pre><code class=\"language-zsh\">kubectl apply -f application_subdirectories_options.yml\n</code></pre>\n<p>with a file <code>application_subdirectories_options.yml</code>\nwith a content:</p>\n<pre><code class=\"language-yaml\">apiVersion: argoproj.io/v1alpha1\nkind: Application\nmetadata:\n  name: directory-app\n  namespace: argocd\nspec: \n  destination:\n    namespace: directory-app\n    server: \"https://kubernetes.default.svc\"\n  project: default\n  source:\n    path: guestbook-with-sub-directories\n    repoURL: \"https://github.com/mabusaa/argocd-example-apps.git\"\n    targetRevision: master\n    directory:\n      recurse: true\n  syncPolicy:\n    syncOptions:\n      - CreateNamespace=true\n</code></pre>\n<p>Here are ArgoCD options which can be adjusted with kustomize:</p>\n<ul>\n<li>Name prefix: appended to resources</li>\n<li>Name suffix: appended to resources</li>\n<li>Images: to override images</li>\n<li>Common labels: set labels on all resources</li>\n<li>Common annotations: set annotations on all resources</li>\n<li>Version: explicitly set kustomize version</li>\n</ul>\n<p>The manifest below called for instance <code>application_kustomize.yaml</code> is an example of an kustomize argocd application. Two options are adjusted with kustomize: namePrefix\nand a commonLabel with a key of app and a value of demo. </p>\n<pre><code class=\"language-yaml\">apiVersion: argoproj.io/v1alpha1\nkind: Application\nmetadata:\n  name: kustomize-app\n  namespace: argocd\nspec: \n  destination:\n    namespace: kustomize-app\n    server: \"https://kubernetes.default.svc\"\n  project: default\n  source:\n    path: kustomize-guestbook\n    repoURL: \"https://github.com/mabusaa/argocd-example-apps.git\"\n    targetRevision: master\n    kustomize:\n      namePrefix: staging-\n      commonLabels:\n        app: demo\n  syncPolicy:\n    syncOptions:\n      - CreateNamespace=true\n</code></pre>\n<p>Application can be started with:</p>\n<pre><code class=\"language-zsh\">kubectl apply -f application_kustomize.yml\n</code></pre>\n<p>Note that Argo CD automatically detects that\nit is a Kustomize application.</p>\n<h2>Projects</h2>\n<p>Display info on by default project:</p>\n<pre><code class=\"language-zsh\">kubectl get appproject -n argocd -o yaml\n</code></pre>\n<p>Now a project can be created by running </p>\n<pre><code class=\"language-zsh\">kubectl apply -f ./project.yaml\n</code></pre>\n<p>with a file called project.yaml: </p>\n<pre><code class=\"language-yaml\">apiVersion: argoproj.io/v1alpha1\nkind: AppProject\nmetadata:\n  name: demo-project\n  namespace: argocd\nspec:\n  description: Demo Project\n  sourceRepos:\n  - '*'\n\n  destinations:\n  - namespace: '*'\n    server: '*'\n\n  clusterResourceWhitelist:\n  - group: '*'\n    kind: '*'\n\n  namespaceResourceWhitelist:\n  - group: '*'\n    kind: '*'\n</code></pre>\n<p>Display info on projects again with:</p>\n<pre><code class=\"language-zsh\">kubectl get appproject -n argocd -o yaml\n</code></pre>\n<p>An application can be defined to start in the project thus created with:</p>\n<pre><code class=\"language-zsh\">kubectl apply -f './application.yml'\n</code></pre>\n<p>with the following content for application.yml:</p>\n<pre><code class=\"language-yaml\">apiVersion: argoproj.io/v1alpha1\nkind: Application\nmetadata:\n  name: guestbook-demo-project\n  namespace: argocd\nspec: \n  destination:\n    namespace: guestbook-demo-project\n    server: \"https://kubernetes.default.svc\"\n  project: demo-project\n  source:\n    path: guestbook\n    repoURL: \"https://github.com/mabusaa/argocd-example-apps.git\"\n    targetRevision: master\n  syncPolicy:\n    syncOptions:\n      - CreateNamespace=true\n</code></pre>\n<h2>Sync</h2>\n<p>Automated syncing can be enabled by declaring a syncPolicy in the\nmanifest of the application. Or by adding a <code>sync-policy automated</code>\nflag to a cli <code>argocd app create</code> command. There is also a\nSYNC POLICY setting which can be set to Automatic in the Web UI.</p>\n<p>Example of an application manifest:</p>\n<pre><code class=\"language-yaml\">apiVersion: argoproj.io/v1alpha1\nkind: Application\nmetadata:\n  name: auto-sync-app\n  namespace: argocd\nspec: \n  destination:\n    namespace: auto-sync-app\n    server: \"https://kubernetes.default.svc\"\n  project: default\n  source:\n    path: guestbook-with-sub-directories\n    repoURL: \"https://github.com/mabusaa/argocd-example-apps.git\"\n    targetRevision: master\n    directory:\n      recurse: true\n  syncPolicy:\n    automated: {}\n    syncOptions:\n      - CreateNamespace=true\n</code></pre>\n<ul>\n<li>\n<p>Additional features: </p>\n<ul>\n<li>automated pruning</li>\n<li>self healing</li>\n</ul>\n</li>\n<li>\n<p>Sync Options with at the resource level with annotations or at the\napplication level with syncOptions (in syncPolicy):</p>\n<ul>\n<li>Prune = false</li>\n<li>Validate = false</li>\n</ul>\n</li>\n<li>\n<p>Selective syncing at the application level only with syncOptions:\nApplyOutOfSyncOnly=true</p>\n</li>\n<li>\n<p>Argo waves with PruneLast=true at application or resource level.</p>\n</li>\n<li>\n<p>Replacing resources: by default Argo CD uses <code>kubectl apply</code> to\ndeploy resources changes\nIn some cases, you need to replace/recreate the resources.\nArgoCD can do this by using replace=true. It can be done at application level\nwith a Replace of true in syncOptions. It can be also done at resource level\nwith an annotation like <code>argocd.argoproj.io/sync-options: Replace=true</code></p>\n</li>\n<li>\n<p>sync can be configured to fail if resource is found in other applications\nby using FailOnSharedResource=true</p>\n</li>\n</ul>","id":"/Users/nicolas/repos/github.com/nperon/workshop/src/pages/2022-08-14-argocd/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-08-14T22:51:00.823Z","path":"/argocd","title":"Argo CD","excerpt":"","tags":["continuous delivery","cloud","devops","gitops","kubernetes","kustomize"]}},{"html":"<h2>Deleting a commit</h2>\n<p>The --soft flag ensures changes from the deleted commit remain staged:</p>\n<pre><code class=\"language-zsh\">git reset --soft HEAD~1\n</code></pre>\n<h2>Rebasing to the tip of local main branch</h2>\n<pre><code class=\"language-zsh\">git rebase main\ngit push --force-with-lease\n</code></pre>\n<p>In case there are conflict execute commands like <code>git add ...</code> and <code>git rebase --continue</code></p>\n<p>When relevant, priority can be given to feature branch changes with:</p>\n<pre><code class=\"language-zsh\">git rebase --main -Xtheirs\n</code></pre>\n<p>followed with:</p>\n<pre><code class=\"language-zsh\">git push --force\n</code></pre>\n<h2>Updating the local branch with respect to remote</h2>\n<pre><code class=\"language-zsh\">git pull --rebase\n</code></pre>\n<h2>Interactive rebase</h2>\n<pre><code class=\"language-zsh\">git log --oneline --decorate --all --graph\n</code></pre>\n<pre><code class=\"language-zsh\">git rebase --interactive HEAD~3\n</code></pre>\n<pre><code class=\"language-zsh\">git push --force origin feat/add-my-feature\n</code></pre>\n<h2>Get back to older commit after several commits pushed to the remote</h2>\n<p>Use <code>git log</code> and <code>git reflog</code> to identify the\ncommit to target and get its sha1, say COMMIT_HASH</p>\n<p>Get back to that commit with:</p>\n<pre><code class=\"language-zsh\">git reset --hard COMMIT_HASH\n</code></pre>\n<p>Finally, push with:</p>\n<pre><code class=\"language-zsh\">git push origin feat/opensearch_operator --force-with-lease\n</code></pre>","id":"/Users/nicolas/repos/github.com/nperon/workshop/src/pages/2022-08-20-git/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-08-20T23:16:22.342Z","path":"/git","title":"Git","excerpt":"","tags":["git","devops"]}},{"html":"<h2>PromQL</h2>","id":"/Users/nicolas/repos/github.com/nperon/workshop/src/pages/2022-09-03-prometheus/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-09-03T14:02:00.438Z","path":"/prometheus","title":"Prometheus","excerpt":"","tags":["devops","monitoring","alerting","telemetry","prometheus"]}},{"html":"<h2>Goals</h2>\n<pre><code class=\"language-zsh\">mvn clean\n</code></pre>\n<pre><code class=\"language-zsh\">mvn package\n</code></pre>\n<p>To display a POM that includes all of the inherited properties, execute:</p>\n<pre><code class=\"language-zsh\">mvn help:effective-pom\n</code></pre>\n<h2>Dependency Scope</h2>\n<ul>\n<li>Compile: Default. Available on all classpaths of project. Also, propagated to downstream projects.</li>\n<li>Provided: Like Compile, but expected to be provided by JDK or container at runtime.</li>\n<li>Runtime: Not required for compile, but needed for runtime. On runtime and test classpaths, not compile.</li>\n<li>Test: Only available on test classpath, not transitive.</li>\n<li>System: similar to provided, but jar is added to system explicitly (via file path)</li>\n<li>Import: imports dependency of POM</li>\n</ul>\n<h4>Dependency plugin goals</h4>\n<ul>\n<li>dependency:tree</li>\n<li>dependency:go-offline</li>\n<li>dependency:purge-local-repository</li>\n<li>dependency:sources</li>\n</ul>\n<h2>Maven Build Lifecycles</h2>\n<p>Maven is based on the concept of build lifecycles.\nA licecycle is a pre-defined group of build steps called <strong>phases</strong>.\nEach phase can be bound to one or more plugin <strong>goals</strong>.\nAll work done in Maven is done by plugins.\nLifecycles and phases provide the framework to call plugin goals in a sequence.</p>\n<p>There are three pre-defined lifecycles:</p>\n<ul>\n<li>clean</li>\n<li>default: does the build and deployment of the project. It is defined without plugin bindings, bindings are defined for each packaging. The default lifecycle include the phases but a number of them are not mentioned: validate, compile, test, package, verify, install and deploy.</li>\n<li>site: least used in practice</li>\n</ul>\n<p>For instance phases of the default lifecycle involved in the case of a jar\npackaging are the following:</p>\n<ul>\n<li>process-resources (maven-resources-plugin: resources)</li>\n<li>compile (maven-compiler-plugin: compile)</li>\n<li>process-test-resources (maven-compiler-plugin: testResources)</li>\n<li>test-compile (maven-compiler-plugin: testCompile)</li>\n<li>test (maven-surefire-plugin: jar)</li>\n<li>package (maven-jar-plugin: jar)</li>\n<li>install (maven-install-plugin: install)</li>\n<li>deploy (maven-deploy-plugin: deploy)</li>\n</ul>\n<h2>Maven configuration — <code>settings.xml</code></h2>","id":"/Users/nicolas/repos/github.com/nperon/workshop/src/pages/2022-08-27-maven/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-08-27T00:47:00.342Z","path":"/maven","title":"Maven","excerpt":"","tags":["maven","devops","continuous integration","build","java","kotlin"]}},{"html":"<p>A list of all tasks can be displayed with:</p>\n<pre><code class=\"language-zsh\">gradle tasks\n</code></pre>\n<h3>Gradle init task:</h3>\n<p>This task initiates a project including a gradlew wrapper script:</p>\n<pre><code class=\"language-zsh\">mkdir demo\ncd demo\ngradle init\ntree .\n</code></pre>\n<p>Application can be run with:</p>\n<pre><code class=\"language-zsh\">./gradlew run\n</code></pre>\n<p>and bundled with:</p>\n<pre><code class=\"language-zsh\">./gradlew build\n</code></pre>\n<p>This produces two archives called <code>app.tar</code> and <code>app.zip</code> in `<code></code>app/build/distribution<code></code></p>\n<h3>Gradle jar task: <a href=\"https://docs.gradle.org/current/samples/sample_building_kotlin_libraries.html#customize_the_library_jar\">customizing the library jar</a></h3>\n<p>Properties to set if required in the build.gradle.kts file: version, name and group i.e. maven coordinates:</p>\n<pre><code class=\"language-kts\">version=\nname=\ngroup=\n</code></pre>\n<p>Run the jar task:</p>\n<pre><code class=\"language-zsh\">./gradlew jar\n</code></pre>\n<p>Verifying that an archive is valid:</p>\n<pre><code class=\"language-zsh\">jar tf lib/build/libs/lib.jar\n</code></pre>\n<p>Verifying that archive is signed:</p>\n<pre><code class=\"language-zsh\">jarsigner -verify lib/build/libs/lib.jar\n</code></pre>\n<h3>Displaying the tasks sequence</h3>\n<pre><code class=\"language-zsh\">./gradlew test --console=plain\n</code></pre>\n<h3>The dependencies task</h3>\n<pre><code class=\"language-zsh\">./gradlew :app:dependencies --configuration runtimeClasspath\n</code></pre>\n<p>This shows dependencies of a given classpath, e. g. runtime.</p>\n<h3>Generating sources JAR:</h3>\n<pre><code class=\"language-kts\">java {\n    withSourcesJar()\n}\n</code></pre>\n<h3>Publishing</h3>\n<ul>\n<li><a href=\"https://docs.gradle.org/current/userguide/publishing_setup.html#sec:basic_publishing\">Basic Publishing</a></li>\n<li><a href=\"https://docs.gradle.org/current/userguide/publishing_signing.html#publishing_maven:signing\">Signing Artifacts</a></li>\n</ul>","id":"/Users/nicolas/repos/github.com/nperon/workshop/src/pages/2022-09-09-gradle/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2022-09-09T14:02:00.438Z","path":"/gradle","title":"Gradle","excerpt":"","tags":["gradle","devops","continuous integration","build","java","kotlin"]}},{"html":"<h2>Terminology</h2>\n<ul>\n<li>kubernetes / k8s / kube: the whole orchestration system</li>\n<li>kubeclt aka cube control: cli to configure kubernetes and manage apps</li>\n<li>node: single server in the k8s cluster</li>\n<li>kubelet: k8s agent running on nodes</li>\n<li>each kubelet can have a kube-proxy controlling its networking</li>\n<li>control plane aka the \"master\": set of containers that manage the cluster. Includes api server, scheduler, controller manager, etcd, core DNS and more</li>\n</ul>\n<p>A possible local Kubernetes environment could be composed of the following applications: </p>\n<ul>\n<li>minikube: a local cluster which can be installed and run on your machine. </li>\n<li>kubectl: the interface to interact with the cluster</li>\n<li>k9s: a CLI to monitor and manage your local kubernetes clusters</li>\n</ul>\n<!-- ## Links\n\nKubernetes in a browser:  \n\ntry [http://play-with-k8s.com](http://play-with-k8s.com)\nor [katacoda.com](katacoda.com) in browser -->\n<h1>Local install</h1>\n<p>Follow documentation on <a href=\"https://minikube.sigs.k8s.io/docs/start/\">this page</a> to install minikube locally.</p>\n<p>You can then check your minikube status with: </p>\n<pre><code class=\"language-bash\">minikube status\n</code></pre>\n<p>It can be started if necessary with</p>\n<pre><code class=\"language-bash\">minikube start\n</code></pre>\n<p>Install now kubectl following documentation on <a href=\"https://kubernetes.io/docs/tasks/tools/install-kubectl-linux/\">this page</a>. </p>\n<p>The <a href=\"https://kubernetes.io/docs/tasks/tools/install-kubectl-linux/#optional-kubectl-configurations-and-plugins\">Optional kubectl configurations and plugins</a> can be skipped in a first stage.</p>\n<p>Configuration can be verified with:</p>\n<pre><code class=\"language-bash\">kubectl cluster-info\n</code></pre>\n<p>You can find out the location of your kubectl executable with:</p>\n<pre><code class=\"language-bash\">which kubectl\n</code></pre>\n<p>Download the <code>k9s_Linux_x86_64.tar.gz</code> archive available on\n<a href=\"https://github.com/derailed/k9s/releases\">this page</a> from the k9s github.\nOnce it is extracted, just execute k9s with:</p>\n<pre><code class=\"language-bash\">./k9s\n</code></pre>\n<h2>Pod commands together with a proper example to apply them:</h2>\n<p><code>kubectl get pod</code><br>\nget information about all running pods  </p>\n<p><code>kubectl describe pod &#x3C;pod></code><br>\ndescribe one pod  </p>\n<p><code>kubectl expose pod &#x3C;pod> --port=444 --name=frontend</code><br>\nexpose the port of a pod (creates a new service)  </p>\n<p><code>kubectl port-forward &#x3C;pod> 8080</code><br>\nport forward the exposed pod port to your local machine  </p>\n<p><code>kubectl attach &#x3C;podname> -i</code><br>\nattach to the pod  </p>\n<p><code>kubectl exec &#x3C;pod> -- command</code><br>\nexecute a command on the pod  </p>\n<p><code>kubectl label pods &#x3C;pod> mylabel=awesome</code><br>\nadd a new label to a pod  </p>\n<p><code>kubectl run -i --tty busybox --image=busybox --restart=Never -- sh</code><br>\nrun a shell in a pod  </p>\n<p>With the following example of a pod description\nin a file called <code>helloworld.yml</code>,</p>\n<pre><code class=\"language-yaml\">apiVersion: v1\nkind: Pod\nmetadata:\n  name: nodehelloworld.example.com\n  labels:\n    app: helloworld\nspec:\n  containers:\n  - name: k8s-demo\n    image: wardviaene/k8s-demo\n    ports:\n    - name: nodejs-port\n      containerPort: 3000\n</code></pre>\n<p>The pod can be created with:</p>\n<pre><code class=\"language-bash\">kubectl create -f helloworld.yml\n</code></pre>\n<p>Then, local port 8081 can be forwarded to port 3000 of the pod with:</p>\n<pre><code class=\"language-bash\">kubectl port-forward nodehelloworld.example.com 8081:3000\n</code></pre>\n<p>Or else we can create a service of type NodePort to expose the pod with:</p>\n<pre><code class=\"language-bash\">kubectl expose pod nodehelloworld.example.com --type=NodePort --name nodehelloworld-service\n</code></pre>\n<p>The end point to that service from the local machine can be displayed with:</p>\n<pre><code class=\"language-bash\">minikube service nodehelloworld-service --url\n</code></pre>\n<p>IP addresses of services within the cluster are different. They can be accessed with:</p>\n<pre><code class=\"language-bash\">kubectl get service\n</code></pre>\n<p>It is possible to attach to the pod and watch the possible logs with:</p>\n<pre><code class=\"language-bash\">kubectl attach nodehelloworld.example.com\n</code></pre>\n<p>To execute a command like <code>ls /app</code> run the following line:</p>\n<pre><code class=\"language-bash\">kubectl exec nodehelloworld.example.com -- ls /app\n</code></pre>\n<p>It is instructive to run the two following commands now:</p>\n<pre><code class=\"language-bash\">kubectl exec nodehelloworld.example.com -- touch /app/test.txt\nkubectl exec nodehelloworld.example.com -- ls /app\n</code></pre>\n<p>To operate with a bash prompt in a pod use the following command: </p>\n<pre><code class=\"language-bash\">kubectl exec nodehelloworld.example.com -i -t -- /bin/bash\n</code></pre>\n<p>A description of the pod can be displayed with:</p>\n<pre><code class=\"language-bash\">kubectl describe service nodehelloworld-service\n</code></pre>\n<p>Launch another pod based on the busybox image with:</p>\n<pre><code class=\"language-bash\">kubectl run -i --tty busybox --image=busybox --restart=Never -- sh\n</code></pre>\n<p>Let us assume that the endpoint of our nodehelloworld-service displayed in its description was 172.17.0.2:3000. Then, commands can be executed in the shell of our busybox like:</p>\n<pre><code class=\"language-bash\">ls\ntelnet 172.17.0.2 3000\n</code></pre>\n<p>Pods can finally be deleted with:</p>\n<pre><code class=\"language-bash\">kubectl delete pod busybox\nkubectl delete pod nodehelloworld.example.com\n</code></pre>\n<p>You can also delete the service nodehelloworld-service with:</p>\n<pre><code class=\"language-bash\">kubectl delete service nodehelloworld-service\n</code></pre>\n<h2>Starting a cluster with an nginx container</h2>\n<p>Create a file called <code>deployment.yaml</code> with the following content:</p>\n<pre><code class=\"language-yaml\">apiVersion: apps/v1\nkind: Deployment\nmetadata:\n  name: nginx-deployment\n  labels:\n    app: nginx\nspec:\n  selector:\n    matchLabels:\n      app: nginx\n  template:\n    metadata:\n      labels:\n        app: nginx\n    spec:\n      containers:\n      - name: nginx\n        image: nginx:1.21.4\n        ports:\n        - containerPort: 80\n</code></pre>\n<p>Create another file called <code>service.yaml</code> with the following content:</p>\n<pre><code class=\"language-yaml\">apiVersion: v1\nkind: Service\nmetadata:\n  name: nginx-service\n  labels:\n    app: nginx\nspec:\n  selector:\n    app: nginx\n  ports:\n    - protocol: TCP\n      port: 80\n      targetPort: 80\n  type: LoadBalancer\n</code></pre>\n<p>Cluster can now be started with:</p>\n<pre><code class=\"language-bash\">kubectl apply -f deployment.yaml\n</code></pre>\n<p>followed with:</p>\n<pre><code class=\"language-bash\">kubectl apply -f service.yaml\n</code></pre>\n<p>To display services execute:</p>\n<pre><code class=\"language-bash\">kubectl get services\n</code></pre>\n<p>The following command is an interesting one to run now:</p>\n<pre><code class=\"language-bash\">minikube service nginx-service\n</code></pre>\n<p>since it displays the url of the nginx-service and opens the latter\nservice in your default browser.</p>\n<p>To display namespaces execute:</p>\n<pre><code class=\"language-bash\">kubectl get namespaces\n</code></pre>\n<p>To find out the namespace where pods have been created you can run this command:</p>\n<pre><code class=\"language-bash\">kubectl get pods --all-namespaces\n</code></pre>\n<p>To stop one of the pods displayed whose name is for instance <code>hello-minikube-6ddfcc9757-h4ctx</code>\nexecute the following command:</p>\n<pre><code class=\"language-bash\">kubectl delete -n default pod hello-minikube-6ddfcc9757-h4ctx\n</code></pre>\n<p>To display deployments currently running execute:</p>\n<pre><code class=\"language-bash\">kubectl get deployments\n</code></pre>\n<p>To delete a service first display all of your services with:</p>\n<pre><code class=\"language-bash\">kubectl get service -o wide\n</code></pre>\n<p>You can now pick the one you wish to delete from the displayed list\n--- for instance nginx-service --- and then delete it with:</p>\n<pre><code class=\"language-bash\">kubectl delete service nginx-service\n</code></pre>\n<p>Delete deployment nginx-deployment with:</p>\n<pre><code class=\"language-bash\">delete deployment nginx-deployment\n</code></pre>\n<h2>Minikube example</h2>\n<pre><code class=\"language-bash\">kubectl create deployment hello-minikube --image=k8s.gcr.io/echoserver:1.4\n</code></pre>\n<pre><code class=\"language-bash\">kubectl expose deployment hello-minikube --type=NodePort --port=8080\n</code></pre>\n<pre><code class=\"language-bash\">minikube service hello-minikube\n</code></pre>\n<pre><code class=\"language-bash\">kubectl delete deployment hello-minikube\n</code></pre>\n<pre><code class=\"language-bash\">minikube stop\n</code></pre>\n<p>The minikube vm can optionally be completely reset with:</p>\n<pre><code class=\"language-bash\">minikube delete\n</code></pre>\n<p>After this, Minikube will start from scratch the next time it is started.</p>\n<h2>Advanced topics</h2>\n<h4>Service discovery</h4>\n<pre><code class=\"language-zsh\">kubectl run -i --tty busybox --image=busybox --restart=Never -- sh\n</code></pre>\n<pre><code class=\"language-sh\">cat /etc/resolv.conf\n</code></pre>\n<pre><code class=\"language-zsh\">kubectl exec database -i -t -- mysql -u root -p\n</code></pre>\n<h4>ConfigMap</h4>\n<pre><code class=\"language-sh\">kubectl create configmap app-config --fromfile=app.properties\n</code></pre>\n<h4>Ingress Controller</h4>\n<h4>External DNS</h4>\n<h4>Volumes</h4>\n<h4>K9S</h4>\n<pre><code class=\"language-sh\">k9s help\n</code></pre>\n<pre><code class=\"language-sh\">k9s -n my-namespace\n</code></pre>\n<p>The : mode --- typing : followed with a kubernetes concept:</p>\n<pre><code class=\"language-sh\">:ns\n:pod\n:svc\n:deploy\n:ing\n:crd\n:pulse\n:popeye\n:xray\n</code></pre>\n<p><code>popeye</code>  displays score card for the cluster. Takes one to the sanitizer.</p>\n<p><code>xray pod default</code> brings up windows like file explorer for folder structure navigation</p>\n<p>The / mode for filtering --- example:</p>\n<p><code>/ followed with -l version=v2</code> filters by labels version of v2</p>\n<p>Killing a pod can be done with ctrl + d</p>","id":"/Users/nicolas/repos/github.com/nperon/workshop/src/pages/2021-11-25-kubernetes/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2021-11-25T10:17:00.823Z","path":"/kubernetes","title":"Kubernetes","excerpt":"","tags":["kubernetes","cloud","devops"]}}],"tagName":"devops"}}

/***/ })

});


